import ReactEcharts from "echarts-for-react"

const options = {
    tooltip: {
        trigger: 'axis',
        axisPointer: {
            type: 'shadow'
        }
    },
    legend: {},
    grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
    },
    xAxis: {
        type: 'value',
        boundaryGap: [0, 0.01]
    },
    yAxis: {
        type: 'category',
        data: ['Maiwa', 'Enrekang', 'Baraka', 'Masalle', 'Anggeraja', 'Cendana']
    },
    series: [{
        type: 'bar',
        data: [18203, 23489, 29034, 104970, 131744, 630230],
        color: '#499841'
    }
    ]
}

function UsahaDiWilayah() {
    return (
        <ReactEcharts
            option={options}
            style={{ width: "auto", height: "400px" }}
        ></ReactEcharts>
    )
}

export default UsahaDiWilayah
